.contacts {
  display: flex;
  align-items: center;
  .links {
    text-decoration: none;
    letter-spacing: 2px;
    font-size: 1.2rem;
    color: #fff;
    transition: all 0.3s;
    position: relative;
    &:hover {
      filter: brightness(100%);
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 0;
      height: 1px;
      background: #fff;
      transition: 0.4s;
      right: 0;
      left: initial;
    }
    &:hover:after {
      width: 100%;
      right: initial;
      left: 0;
    }
  }
}
@media (max-width: 600px) {
  .contacts {
    margin-top: -10px;
  }
}
